@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
body {
 margin: 0;
 padding: 0;
 font-family: 'Spartan', sans-serif;
 font-weight: 700;
}

* {
 --Desaturated-Dark-Cyan: #5ba4a4;
 --Light-Grayish-Cyan-Background: #effafa;
 --Light-Grayish-Cyan-Filter-Tablets: #eef6f6; 
 --Dark-Grayish-Cyan: #7b8e8e;
 --Very-Dark-Grayish-Cyan: #2c3a3a;
 box-sizing: border-box;
}

.listings .listing button {
 padding: .5rem .5rem;
 margin: .5rem 1rem .5rem 0;
 color: var(--Desaturated-Dark-Cyan);
 font-family: 'Spartan', sans-serif;
 font-weight: 700;
 font-size: .8rem;
 background: var(--Light-Grayish-Cyan-Background);
 border: 0;
 border-radius: .3rem;
 cursor: url(/static-job-listings/static/media/cursor.611ef8c0.svg), auto;
 transition: all .5s ease-in-out;
}

.listings .listing button:hover {
 background: var(--Desaturated-Dark-Cyan);
 color: #fff;
}

.header {
 width: 100%;
 height: 156px;
 background: var(--Desaturated-Dark-Cyan);
}

.header .desktop {
 display: none;
}

@media only screen and (min-width: 768px) {
 .header .desktop {
  width: 100%;
  height: 100%;
  display: inline-block;
 }
 .header .mobile {
  display: none;
 } 
}

.listings {
 width: 100%;
 padding: 2rem 1.5rem;
 padding-top: 3.5rem;
 display: grid;
 grid-template-columns: repeat(1, 1fr);
 grid-gap: 2.5rem;
 background: var(--Light-Grayish-Cyan-Background);
 position: relative;
}

@media only screen and (min-width: 768px) {
 .listings {
  padding: 4.5rem 10rem;
 } 
}


.listings .filter {
  background: #fff;
  display: flex;
  padding: 0 1.5rem;
  padding-bottom: 1rem;
  align-items: center;
  justify-content: space-between;
  border-radius: .6rem;
  box-shadow: 0 15px 35px -20px #5ba4a4;
  position: fixed;
  top: 7.7rem;
  right: 1.5rem;
  left: 1.5rem;
  z-index: 1;
  opacity: 0;
  transition: all .4s ease-in-out;
}

@media only screen and (min-width: 768px) {
  .listings .filter {
    right: 10rem;
    left: 10rem;
  } 
 }
 

.listings .filter .clear{
  color: var(--Desaturated-Dark-Cyan);
  font-size: .8rem;
  transition: all .4s ease-in-out;
  cursor: url(/static-job-listings/static/media/cursor.611ef8c0.svg), auto;
}

.listings .filter .clear:hover{
  text-decoration: underline;
}

.listings .filter .buttons .tag{
  display: inline-flex;
  align-items: center;
  padding: 0 0;
  padding-left: .5rem;
  border: 0;
  color: var(--Desaturated-Dark-Cyan);
  font-family: 'Spartan', sans-serif;
  font-weight: 700;
  font-size: .8rem;
  background: var(--Light-Grayish-Cyan-Background);
  margin-top: 1rem;
  margin-right: 1rem;
  transition: all .4s ease-in-out;
}

.listings .filter .buttons .tag .close{
  padding: .4rem;
  margin-left: .5rem;
  background: var(--Desaturated-Dark-Cyan);
  cursor: url(/static-job-listings/static/media/cursor.611ef8c0.svg), auto;
  transition: all .4s ease-in-out;
}

.listings .filter .buttons .tag .close:hover{
  background: #2f3b3b;
}

.listings .listing {
 width: 100%;
 padding: 1rem 1.15rem;
 padding-top: 1.5rem;
 display: flex;
 flex-direction: column;
 align-items: flex-start;
 position: relative;
 background: #fff;
 border-radius: .6rem;
 box-shadow: 0 15px 35px -20px #5ba4a4;
}

@media only screen and (min-width: 768px) {
 .listings .listing {
  padding: 1rem 2rem;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
 } 
}

.listings .listing .details {
 width: 100%;
 display: flex;
 flex-direction: column;
 border-bottom: 1px solid #c0c2c1;
}

@media only screen and (min-width: 768px) {
 .listings .listing .details  {
  width: 50%;
  flex-direction: row;
  align-items: center;
  /* justify-content: space-between; */
  border-bottom: 0;
  } 
}

.listings .listing .details .logo {
 position: absolute;
 top: -1.5rem;
 margin-right: 2rem;
}

@media only screen and (min-width: 768px) {
 .listings .listing .details .logo {
  position: static;
 } 
}

.listings .listing .details .logo img{
 width: 3rem;
 height: 3rem;
}

@media only screen and (min-width: 768px) {
 .listings .listing .details .logo img{
  width: 5.4rem;
  height: 5.4rem;
  } 
}


.listings .listing .details .info{
 display: inline-flex;
 align-items: center;
}


.listings .listing .details .info .company{
 margin-right: 1rem;
 font-size: 13px;
 color: var(--Desaturated-Dark-Cyan);
}

@media only screen and (min-width: 768px) {
 .listings .listing .details .info .company{
  font-size: 15px;
 } 
}

.listings .listing .details .info .new{
 color: #fff;
 font-size: 11px;
 padding: .4rem .6rem;
 padding-bottom: .3rem;
 margin-right: .6rem;
 text-transform: uppercase;
 border-radius: 1rem;
 background: var(--Desaturated-Dark-Cyan);
}


.listings .listing .details .info .featured{
 color: #fff;
 font-size: 11px;
 padding: .4rem .5rem;
 padding-bottom: .3rem;
 text-transform: uppercase;
 border-radius: 1rem;
 background: #2f3b3b;
}


.listings .listing .details h2{
 font-size: .95rem;
 margin: 0;
 color: #313737;
 transition: all .4s ease-in-out;
 cursor: url(/static-job-listings/static/media/cursor.611ef8c0.svg), auto;
}

.listings .listing .details h2:hover{
 color: var(--Desaturated-Dark-Cyan);
}

@media only screen and (min-width: 768px) {
 .listings .listing .details h2{
  font-size: 1.15rem;
 } 
}


.listings .listing .details .post-info .bullet{
 width: 3px;
 height: 3px;
 border-radius: 50%;
 background: var(--Dark-Grayish-Cyan);
 margin: 0 .5rem;
}

@media only screen and (min-width: 768px) {
 .listings .listing .details .post-info .bullet{
 margin: 0 1rem;
 } 
}


.listings .listing .details .post-info{
 font-weight: 500;
 padding-top: .4rem;
 padding-bottom: .5rem;
 display: inline-flex;
 align-items: center;
 font-size: 13px;
 color: var(--Dark-Grayish-Cyan);
}

@media only screen and (min-width: 768px) {
 .listings .listing .details .post-info{
  font-size: 15px;
  padding: .0;
 } 
 .listings .listing .details .post-info p{
  font-size: 15px;
  margin-top: .7rem;
 } 
}

.listings .listing .job-tags{
 /* width: 100%; */
 margin-top: .5rem;
 display: flex;
 flex-wrap: wrap;
 align-items: center;
}

